.filterWrap {
    border-bottom: 0.2vh solid #000;
    width: 100vw;
}

.filterFlexWrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 2vh;
    flex-wrap: wrap;
}

.filterFlexWrap div {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1vw;
}

.filterFlexWrap div select {
    padding: 1vh 2vw;
    font-size: 3vh;
    font-family: Comfortaa Light;
}

.filterFlexWrap div select:hover {
    cursor: pointer;
}

.inputRangeWrap {
    display: flex;
    flex-direction: column;
}

.inputRangeWrap input {
    width: 20vw;
}

.inputRangeWrap * {
    margin: 0;
}

div.inputRangeInfo {
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 20vw;
}

.filterButtonWrap {
    display: flex;
    justify-content: center;
}

.filterButton {
    padding: 1vh 3vw;
    font-size: 3vh;
    background-color: #fff;
    border: 0.1vh solid #000;
    margin: 2vh 4vw;
    font-family: Comfortaa Light;
}

.filterButton:hover {
    cursor: pointer;
}

.filterReset {
    border: none;
    background-color: #fff;
    font-size: 2.5vh;
    text-decoration: underline;
    margin: 2vh 4vw;
    font-family: Comfortaa Light;
}

.filterReset:hover {
    cursor: pointer;
}

option {
    font-size: 2.5vh;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
    .filterFlexWrap {
        flex-direction: column;
    }
    .filterFlexWrap div {
        margin: 0.5vh 1vw;
    }
    .filterFlexWrap div select {
        width: 40vw;
        font-size: 2vh;
    }
    .filterButton {
        font-size: 2vh;
    }
    .filterReset {
        font-size: 2vh;
    }
}

@media screen and (max-width: 500px) {
    .filterFlexWrap {
        flex-direction: column;
    }
    .filterFlexWrap div select {
        margin: 0;
        width: 40vw;
        font-size: 1.5vh;
        padding: 0.5vh 1vw;
        border: 0.1vh solid #000
    }

    .inputRangeWrap input{
        width: 40vw;
    }
    div.inputRangeInfo {
        width: 40vw;
    }
    .inputRangeInfo p {
        font-size: 1.5vh;
    }
    .filterButton {
        font-size: 1.5vh;
    }
    .filterReset {
        font-size: 1.5vh;
    }
    
}