.womanCategoryContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(../../../img/womanCatalog.png);
    background-size: cover;
    background-repeat: no-repeat;
    width: 49.3vw;
    margin-right: 0.1vw;
}

.womanCategory {
    display: flex;
    justify-content: center;
    align-items: center;
}

.womanCategoryName {
    color: #fff;
    font-family: Comfortaa Light;
    font-size: 5vh;
}

@media screen and (max-width: 450px) {
    .womanCategoryContainer {
        width: 100vw;
        min-height: 25vh;
        background-position: 0 -20vh;
    }
    .womanCategory {
        width: 100vw;   
        min-height: 25vh;
        border-bottom: 0.4vh solid #fff;
    }
    .womanCategoryName {
        font-size: 3vh;
    }
    
}