.basket {
    display: flex;
    flex-direction: column;
}

.basketExitWrap {
    margin: 2vh 4vw;
    height: 5vh;
    text-align: right;
}
.basketExit {
    background-size: cover;
    width: 3vw;
    height: 3vw;
    display: inline-block;
}

.basketContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 10vw 4vw 10vw;
    min-height: 37vh;
    width: 80vw;
}

.basketInfo {
    border-top: 0.2vh solid #000;
    padding: 2vh 10vw;
}

.basketInfo p {
    font-family: Comfortaa Light;
}

.makeOrderButton {
    padding: 1vh 3vw;
    font-size: 3vh;
    background-color: #fff;
    border-radius: 0.2vw;
    border: 0.2vh solid black;
    font-family: Comfortaa Light;
}

@media screen and (min-width: 451px) and (max-width: 1100px) {
    .basketContent {
        min-height: 46vh;
    }
    .basketExitWrap {
        margin: 2vh 20vw;
    }
    .makeOrderButton {
        font-size: 1.5vh;
    }
}

@media screen and (max-width: 450px) {
    .basketContent {
        min-height: 54vh;
    }
    .basketExit {
        width: 6vw;
        height: 6vw;
    }
    .basketInfo p {
        font-size: 1.5vh;
    }
    .makeOrderButton {
        font-size: 1.5vh;
    }
}