.orderProductWrap {
    width: 35vw;
    margin: 1vh 2vw;
    border: 0.1vh solid black;
}

.orderProduct {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.orderProductImg {
    width: 10vw;
    height: 10vw;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.orderProduct p {
    padding-top: 6vh;
}

.orderProductName {
    margin: 0;
    padding-left: 4vw;
    min-width: 10vw;
    font-family: Comfortaa Light;
}

.orderProductCost {
    margin: 0;
    min-width: 5vw;
    font-family: Comfortaa Light;
}

@media screen and  (max-width: 1100px) {
    .orderProductWrap {
        width: 80vw;
    }
    .orderProduct {
        width: 70vw;
    }
    .orderProductImg {
        width: 15vw;
        height: 15vw;
    }
    .orderProduct p {
        font-size: 1.5vh;
        padding-top: 2vh
    }
    .orderProductName {
        font-size: 1.5vh;
    }
    .orderProductCost {
        font-size: 1.5vh;
    }
}

