.orderWrap {
    display: flex;
    flex-direction: row;
}

.orderSection {
    width: 50vw;
    height: 85vh; 
}

.orderFormWrap {
    border-right: 0.2vh solid black;
}

.orderForm {
    display: flex;
    height: 50vh;
    margin: 15vh 15vw 15vh 5vw;
    flex-direction: column;
    justify-content: space-evenly;
}

.orderForm h2 {
    margin: 1vh 0;
    font-size: 5vh;
    font-family: Comfortaa Light;
}

.orderForm input, select {
    margin: 1vh 0;
    width: 20vw;
    padding: 1vh 2vw;
    border-radius: 0.2vw;
    border: 0.2vh solid black;
    font-size: 3vh;
}

.orderForm a:hover {
    cursor: auto;
}

.orderButtonWrap {
    width: 19vw;
}

.orderForm button {
    width: 15vw;
    padding: 1vh 2vw;
    font-size: 3vh;
    margin: 5vh 0 0 0;
    background-color: #fff;
    border-radius: 0.2vw;
    border: 0.2vh solid black;
    font-family: Comfortaa Light;

}

.orderForm button:hover {
    cursor: pointer;
}

.orderInfo {
    border-radius: 0.2vw;
}

.orderProducts {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 65vh;
    overflow-y: scroll;
}

.orderInfo .orderInfoTitle, .orderInfoSum {  
    padding: 3vh 2vw;
    margin: 0;
    font-family: Comfortaa Light;
}

.orderInfoTitle {
    border-bottom: 0.2vh solid #000;
}

.orderInfoSum {
    border-top: 0.2vh solid #000;
}


@media screen and (max-width: 1100px) {
    .orderWrap {
        flex-direction: column;
    }
    .orderSection {
        width: 100vw;
        height: auto;
    }
    .orderFormWrap {
        border:  none;
        border-bottom: 0.2vh solid #000;
        height: 60vh;
    }
    .orderForm {
        margin: 5vh;
    }
    .orderForm h2 {
        font-size: 3vh;
    }
    .orderForm input, select {
        width: 50vw;
        font-size: 1.5vh;
        margin: 0.5vh 0;
    }
    .orderForm button {
        width: 30vw;
        font-size: 1.5vh;
        margin-top: 2vh;
    }
    .orderProducts {
        height: auto;
    }
    .orderInfo .orderInfoTitle, .orderInfoSum {
        padding: 2vh 5vh;
        font-size: 3vh;
    }
}