.product {
    width: 20vw;
    border: 0.1vh solid black;
    margin: 4vh 0;
    padding-bottom: 2vh;
    border-radius: 0.2vw;
    text-align: center;
}

.product:hover {
    cursor: pointer;
}

.productImg {
    width: 20vw;
    height: 20vw;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.productTextStyle {
    font-family: Arial;
    font-weight: lighter;
    padding: 0 1vw;
}

.productDescription {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #000;
    text-align: left;
    min-height: 15vh;
}

.productDescription p {
    font-family: Comfortaa Light;
}

.inBasket {
    border-radius: 0.2vw;
    border: 1px solid black;
    padding: 1vh 1vw;
    font-family: Arial;
    background-color: #fff;
}

.inBasket:hover {
    cursor: pointer;
}

.inBasket p {
    padding: 0.5vh 1vw;
    margin: 0;
    font-size: 1.2em;
    font-family: Comfortaa Light;
}

@media screen and (max-width: 1100px) {
    .product {
        width: 40vw;
        margin: 1vh 0;
    }

    .productImg {
        width: 40vw;
        height: 40vw;
    }

    .productDescription {
        min-height: 10vh;
        padding: 1vh 1vw;
    }

    .inBasket {
        padding: 0.5vh 0.5vw;
    }
}

@media screen and (max-width: 500px) {
    .productDescription {
        min-height: 7vh;
    }
    .productDescription p {
        font-size: 1.5vh;
    }
    .inBasket {
        font-size: 1vh;
    }
}