.sexCategories {
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: stretch;
}

.categoryWrap {
    display: flex;
}

@media screen and (min-width: 451px) and (max-width: 1100px) {
    .sexCategories {
        height: 56vh;
    }
}

@media screen and (max-width: 450px) {
    .sexCategories {
        flex-direction: column;
        height: max-content;
    }
}
