.basketItem {
    display: flex;
    margin: 2vh;
    border: 0.1vh solid black;
    width: 60vw;
    justify-content: space-between;
}

.basketItem div {
    display: flex;
    width: 45vw;
    justify-content: space-between;
}

.basketItemImg {
    width: 15vw;
}

.basketItemName {
    margin: 4vh 4vw;
    font-family: Comfortaa Light;
}

.basketItemCost {
    margin: 4vh 2vw;
    font-family: Comfortaa Light;
}

.basketItem span {
    margin: 1vw;
    width: 2vw;
    height: 2vw;
    background-size: cover;
}

@media screen and (max-width: 1100px) {
    .basketItem {
        width: 80vw;
    }
    .basketItem div {
        width: 60vw;
    }
}

@media screen and (max-width: 451px) {
    .basketItemName {
        font-size: 1.5vh;
    }
    .basketItemCost {
        font-size: 1.5vh;
    }
    .basketItem span {
        width: 4vw;
        height: 4vw;
    }
}